import './App.css';
import LiftContainer from './components/LiftContainer';
import React from 'react';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import { liftReducer } from './reducer';
function App() {
  var noOfFloors = 5;
  var noOfLifts = 1;
  var initialState = [];
  for(var i = 0; i<noOfLifts; i++){
    initialState.push(1);
  }
  const store = createStore(liftReducer, initialState);
  return (
    <Provider store={store}>
      <div className="container-fluid d-flex justify-content-center align-items-center" id="mainContainer">
        <LiftContainer noOfFloors={noOfFloors} noOfLifts={noOfLifts}/>
      </div>
    </Provider>
  );
}

export default App;


