import Floor from './Floor';
import React from 'react';
function LiftContainer(props){
    var floors = []
    for(var i=props.noOfFloors;i>0;i--){
        floors.push(<Floor first={i === 1} last={i === props.noOfFloors}  noOfLifts={props.noOfLifts} noOfFloors={props.noOfFloors} key={i} floorNo={i}></Floor>)
    }
    return (
        <div id="LiftContainer" className="d-flex align-items-center justify-content-center">
            <div id="innerLiftContainer" className="d-flex flex-column">
                {floors}
            </div>
        </div>
    );
}

export default LiftContainer;


